<template>
  <v-card :key="keyform" outlined :width="getSize()" class="transparent">
    <iframe name="dummyframe" id="dummyframe" style="display: none;"></iframe>
    <div v-if="!color1">
      <v-card-title v-if="!sameses" class="justify-center text-center white--text meticula">
        <div v-if="!submitted">Fill Your Information &</div>

        <div v-if="!submitted">We Will Contact You</div>
      </v-card-title>
    </div>
    <form
      action="https://docs.google.com/forms/u/2/d/e/1FAIpQLScOIi81eQ7BbvliskmDu2o_ZsFc8w3EAc_n50ubw9atBkyKZQ/formResponse"
      method="POST"
      v-if="!sameses"
      target="dummyframe"
      id="theForm"
      name="theForm"
    >
      <div :hidden="submitted">
        <v-text-field
          v-model="name"
          :error-messages="nameErrors"
          :counter="20"
          label="Name"
          jsname="YPqjbf"
          name="entry.1519658856"
          class="contentborder"
          :hidden="submitted"
          required
          @input="$v.name.$touch()"
          @blur="$v.name.$touch()"
        ></v-text-field>
        <v-text-field
          v-model="phone"
          :error-messages="phoneErrors"
          :counter="10"
          label="Phone Number"
          name="entry.1339993681"
          class="contentborder"
          :hidden="submitted"
          required
          @input="$v.phone.$touch()"
          @blur="$v.phone.$touch()"
        ></v-text-field>
        <v-text-field
          v-model="email"
          :error-messages="emailErrors"
          label="E-mail"
          name="entry.852294895"
          class="contentborder"
          :hidden="submitted"
          required
          @input="$v.email.$touch()"
          @blur="$v.email.$touch()"
        ></v-text-field>
        <input hidden type="text" id="lname" name="entry.22946558" :value="valForm" />
      </div>
      <div>
        <div v-if="submitted">
          <br />
          <br />
          <br />
          <br class="hidden-sm-and-down" />
          <h3 :class="color22()">Thank You For Your Submission</h3>
        </div>
        <br />
        <v-btn rounded depressed @click="sub" :hidden="submitted" :class="color12()">submit</v-btn>
        <br />
        <br />
      </div>
    </form>
    <div v-if="sameses">
      <br />
      <br />
      <br />
      <br />
      <br class="hidden-sm-and-down" />

      <v-card outlined class="transparent" align="center">
        <h3>Sorry, Only One Submission Per Session Allowed</h3>
      </v-card>
    </div>
  </v-card>
</template>

<script>
import { validationMixin } from "vuelidate";
import {
  required,
  maxLength,
  email,
  minLength,
  numeric,
} from "vuelidate/lib/validators";

export default {
  props: ["valForm", "color1"],
  mixins: [validationMixin],
  validations: {
    name: { required, maxLength: maxLength(20) },
    phone: {
      required,
      numeric,
      maxLength: maxLength(10),
      minLength: minLength(7),
    },
    email: { required, email },
    select: { required },
    checkbox: {
      checked(val) {
        return val;
      },
    },
  },

  data: () => ({
    name: "",
    email: "",
    phone: "",
    extra: "",
    select: null,
    action: "",
    items: ["One-Page Website", "Resume Website", "Full Website", "Other"],
    checkbox: false,
    submitted: false,
    sameses: sessionStorage["submitted"],
    submited: { name: "", email: "", select: "" },
    keyform: 0,
    windowWidth: window.innerWidth,
    trueval: true,
  }),

  computed: {
    checkboxErrors() {
      const errors = [];
      if (!this.$v.checkbox.$dirty) return errors;
      !this.$v.checkbox.checked && errors.push("You must agree to continue!");
      return errors;
    },
    nameErrors() {
      const errors = [];
      if (!this.$v.name.$dirty) return errors;
      !this.$v.name.maxLength &&
        errors.push("Name must be at most 10 characters long");
      !this.$v.name.required && errors.push("Name is required.");
      return errors;
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.email && errors.push("Must be valid e-mail");
      !this.$v.email.required && errors.push("E-mail is required");
      return errors;
    },
    phoneErrors() {
      const errors = [];
      if (!this.$v.phone.$dirty) return errors;
      !this.$v.phone.numeric && errors.push("Must not contain Letters");
      !this.$v.phone.minLength &&
        errors.push("Phone Must be at least 7 numbers long");
      !this.$v.phone.maxLength &&
        errors.push("Phone must be at most 10 characters long");
      !this.$v.phone.required && errors.push("Phone is required.");
      return errors;
    },
  },

  methods: {
    sub() {
      document.getElementById("theForm").submit();
      this.submitted = true;
      sessionStorage["submitted"] = true;
      // this.keyform += 1;
      // this.$v.$touch();
      // console.log(document.getElementById("theForm"));
    },
    getForm() {},
    clear() {
      this.$v.$reset();
      this.name = "";
      this.email = "";
      this.checkbox = false;
    },
    getSize() {
      if (this.windowWidth < 1200) {
        return "";
      } else {
        return "800";
      }
    },
    color12() {
      if (this.color1) {
        return "cyan white--text center1";
      } else {
        return "white cyan--text center1";
      }
    },
    color22() {
      if (this.color1) {
        return "justify-center text-center cyan--text meticula";
      } else {
        return "justify-center text-center white--text meticula";
      }
    },
  },
};
</script>
<style scoped>
h3 {
  font-display: none !important;
  font-family: meticula !important;
  font-size: calc(1.5rem);
  font-weight: lighter;
}

.meticula {
  font-family: meticula !important;
  font-size: calc(1.5rem);
  font-weight: lighter;
}
.center1 {
  width: 40%;
  left: 30%;
}

.contentborder {
  border-style: none none none none;
  border-width: 1px;
}
</style>
