<template>
  <v-footer color="#CACACA">
    <v-card class="transparent" outlined>
      <v-row style="width: 100vw" justify="center">
        <v-col v-for="(item, index) in footCard" class="mx-5" :key="index" :cols="scrnSize()">
          <v-card outlined :style="item.stylec" class="transparent">
            <div class="meticula-medium pt-2 pb-2 grey--text text--darken-3 preSpace">{{item.text}}</div>
            <v-card
              width="120"
              outlined
              v-if="item.vbtn"
              class="meticula-btn justify-center transparent"
