                min-width="112"
                height="28"
                align="center"
                outlined
                v-if="item.vbtn"
                :href="item.route"
                :class="item.vbtnopt"
              >{{item.btnText}}</v-btn>
            </v-card>

            <div class="meticula-small font-weight-bold grey--text text--darken-3">{{item.subTitle}}</div>

            <div class="meticula-small grey--text text--darken-3">{{item.subtText}}</div>
            <div class="meticula-small grey--text text--darken-3">{{item.timeText}}</div>
            <div class="meticula-small pb-2 grey--text text--darken-3">{{item.subText}}</div>
            <div
