            </div>
            <div class="meticula-small grey--text text--darken-3">{{item.timeText2}}</div>
            <div class="meticula-small grey--text text--darken-3">{{item.subText3}}</div>
            <div class="meticula-small pb-2 grey--text text--darken-3">{{item.subText6}}</div>
            <div
              class="meticula-small font-weight-bold grey--text text--darken-3"
            >{{item.subTitle3}}</div>
            <div class="meticula-small grey--text text--darken-3">{{item.subText4}}</div>
          </v-card>
        </v-col>
      </v-row>
    </v-card>
