<script>
export default {
  name: "Footer",
  data: () => ({
    footCard: [
      {
        text: "CONTACT US",
        subTitle: "Address:",
        subText: "7th Circle, Amman, Jordan",
        subTitle2: "Phone:",
        subText2: "+962 6 5822626",
        subText3: "+962 79 9451777",
        subText6: "+962 79 7676762",
        subTitle3: "E-mail:",
        subText4: "Info@shamiderma.com",
        vbtnopt2: "transparent meticula-small",
        vbtnopt: "transparent meticula-small",
        stylec: "left: 5vw;",
      },
      {
        text: "OPENING HOURS",
        subtText: "Saturday - Wednesday",
        timeText: "9:00 AM - 5:00 PM",
        subText2: "Thursday",
        timeText2: "9:00 AM - 2:00 PM",
        vbtnopt: "transparent meticula-small",
        vbtnopt2: "transparent meticula-small",
        stylec: "left: 2.5vw;",
      },
      {
        text: "APPOINTMENT",
        btnText: "Call Now!",
        route: "/contact",
        vbtn: true,
        vbtnopt: "text-transform-none meticula-btn pl-4 mr-1",
        vic: true,
        stylec: "left: 2vw;",
      },
    ],
  }),
  methods: {
    scrnSize() {
      if (window.innerWidth < 1200) {
        return 12;
      } else {
        return 2;
      }
    },
    isMobile() {
      if (
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          navigator.userAgent
        )
      ) {
        return true;
      } else {
        return false;
      }
    },
  },
};
</script>
<style scoped>
.my-style {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 30px;
}

.preSpace {
  white-space: pre;
}

.meticula-big {
  font-family: meticula !important;
  font-size: 70px;
}

.meticula-small {
  font-family: meticula !important;
  font-size: calc(0.9rem);
}

.meticula-medium {
  font-family: meticula !important;
  font-size: calc(1.3rem);
}

.meticula-btn {
